	module.exports = {"data":{"markdownRemark":{"html":"<p>There is a slight bug with the script on the massive, bristly\nspider in the underground streambed.</p>\n<p>Players trying to kill this mob are advised to initiate the fight\nas quickly as possible to prevent a bug that will cause the spider\nto disappear instead of returning to the area below its web to\nwait for more victims. Once the spider has been damaged once, this\nis no longer an issue.</p>\n<p>Sorry for any inconvenience. Hopefully I'll find some time and\nmotivation to fix this. Otherwise, use the mentioned workaround.</p>\n<p>Blent</p>","frontmatter":{"title":"News 1582","date":"October 09, 2017","author":"Blent","desc":"Bug: Massive, Bristly Spider"}}},"pathContext":{"path":"/news/1582"}}

